
.top {
  position: fixed;
  top: 0;
  height: 20%;
  width: 100%;
  background-color: #444;
  text-align: center;
}
.top .logo {
  padding: 10px 0;
}
.top .logo img {
  width: 30%;
}
.top .search .search-input {
  border-radius: 15px;
  width: 60%;
  height: 25px;
  background: url("../../assets/search.png") 5px 3px no-repeat #888;
  padding-left: 22px;
}
.top .search .search-input:hover {
  background-color: white;
}
.top .leder .leder-li {
  display: inline-block;
  padding: 20px 3% 20px 3%;
}
.top .leder .leder-li:hover {
  opacity: 0.3;
}
.top .leder .leder-li .leder-a {
  color: white;
}



